@import "../../global.scss";

.myproject{
    background-color:whitesmoke;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;

    
    h1{
        font-size: 80px;
        overflow: hidden;
    }

    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;
        overflow: hidden;

    }

    .container{
       width: 70%;
       display: flex;
      align-items: center;
      justify-content: center;
      flex-wrap: wrap;
      

        .item{
            width: 45%;
            height: 45%;
            background-color: white;
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            margin-bottom: 25px;
            margin: 10px 20px;

            img{
                width: 100%;
                height: 100%;
            }
         
            .desc{
                text-align: center;
                padding: 10px 20px;
            }
           

        }
       
    }
}