@import "../../global.scss";

.topbar{
    width: 100%;
    height: 70px;
    background-color: $mainColor;
    position: fixed;
    top: 0;
    z-index: 2; //priority set untuk component tu untuk timbolkan component tu
    scroll-snap-type: y mandatory;
        scrollbar-width: none;
        &::-webkit-scrollbar{
            display: none;//hilangkan scroll
        }

.wrapper{
    padding: 10px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .left{
        .websitename{
            font-size: 40px;
            font-weight: 700;
            color: pink;
            text-decoration: none;
        }
    }

}
}