.intro{
background-color:rgb(92, 83, 83);
   display: flex; //to use flex kat bawah kene kat sini type display flex tu
    
    .left{
        flex: 0.5;
        overflow: hidden;

        .imgContainer{
            width: 600px;
            height: 550px;
            margin-top: 50px;
            margin-bottom: 50px;
            background-color: lightpink;
            border-radius: 20%;
            display: flex;
            align-items: left;
            justify-content: left;
            float: right;
            position: relative;

            .top{
                position: absolute;
                top: 8px;
                width: 100%;
                font-size: 50px;
                text-align: center;
            }
            
            img{
                height: 100%;
            }

            .right{
               
                display: grid;
                justify-content: center;
                align-items:center;
                margin: auto;
            }


        }
       
    }
    
    .right{
        flex: 0.5;

        .wrapper{
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;

    
        }
        
    }
}